import {Component} from '@angular/core';
import {AlertController, IonicPage, NavController, NavParams} from 'ionic-angular';


@IonicPage()
@Component({
  selector: 'page-i-recharge-history02',
  templateUrl: 'i-recharge-history02.html',
})



export class IRechargeHistory02Page {
  iRechargeDetails;

  constructor(public navCtrl: NavController,
              public alertCtrl: AlertController,
              public navParams: NavParams) {
    this.iRechargeDetails = JSON.parse(navParams.get("iRechargeDetails"));
  }


  showRechargeDetails = function (listItem) {
    let status = "";
    if (listItem.status == 1) {
      status = "Success";
    }
    else if (listItem.status == 2) {
      status = "Requested";
    }
    else if (listItem.status == 3) {
      status = "Canceled";
    }

    else if (listItem.status == 9) {
      status = "Failed";
    }
    else {
      status = "n/a"
    }
    let alertData = {
      "Transfer ID": listItem.transferID,
      "Recharge ID": listItem.rechargeID,
      "Recharge Date": new Date(listItem.rechargeDate).toLocaleString(),//$filter('date')(listItem.rechargeDate,'dd/MM/yyyy hh:mm:ss a'),
      "Debit Account": listItem.drAccount,
      "Mobile No.": listItem.recipientNo,
      "Package": listItem.recipientPackage,
      "Amount": listItem.amount,//$filter('currency')(listItem.amount,"",2),
      "Status": status
    };

    let radioAlert = this.alertCtrl.create();
    radioAlert.setTitle('Recharge Details');
    radioAlert.setCssClass("alertLogCss");
    radioAlert.setMessage(this.generateMessage(alertData));
    radioAlert.addButton('Cancel');
    radioAlert.addButton({
      text: 'OK',
      handler: data => {

      }
    });
    radioAlert.present();
    setTimeout(() => {
      radioAlert.dismiss();
    }, 30000);
  };
  generateMessage = function (itemValue) {

    let message = "";
    Object.keys(itemValue).forEach(key => {
      let value = itemValue[key];
      message = message +
        '<ion-item style="margin-top: 16px"><b>' + key.replace("_", " ") + " : " + "</b>" + value + '<br></ion-item>';
    });
    return message;
  };

  ionViewDidLoad() {

  }

}



